import React from "react";
import { Container, Row, Col, Accordion, ListGroup, Button } from "react-bootstrap";
import AccordionHeader from "react-bootstrap/esm/AccordionHeader";
import Resumepic from "../assets/resumepic.JPG";
import ResumeDownload from "../assets/KalaniMojicaResume.pdf";

const skillTypes = [
	{
		name: "Frontend Development",
		skills: [
			"HTML",
			"CSS",
			"JavaScript",
			"React",
			"Bootstrap",
			"Git",
		]
	}, {
		name: "Development Tools",
		skills: [
			"Git",
			"npm",
			"Jest",
			"Webpack"
		]
	}, {
		name: "Database Skills",
		skills: [
			"MySQL",
			"Sequelize",
			"NoSQL",
			"GraphQL",
			"MongoDB",
			"Mongoose"
		]
	},
];

	function Resume() {
		return (
			<Container>
				<Row>
					<Col>
					<div className="jumbotron jumbotron-fluid">
						<Container>
                        <h1 className="text-center top-title">Resume</h1>
                        <hr></hr>
						</Container>
					</div>
					</Col>
				</Row>
				<Row>
					<Col className="text-center mb-3">
						
						<div className="justify-content-md-center">
						
						<img  src={Resumepic}/>
				
						</div>
						<Button variant="outline-dark" href={ResumeDownload}>Download My Resume</Button>
					</Col>
				</Row>
				<Row>
					<Col>
						<Accordion>
							{skillTypes.map((skillCategory, evtKey) => {
								return(
								<Accordion.Item eventKey={evtKey}>
									<AccordionHeader>{skillCategory.name}</AccordionHeader>
									<Accordion.Body>
									<ListGroup>
										{skillCategory.skills.map((skill) => {
											return (
											<ListGroup.Item>{skill}</ListGroup.Item>
											);
										})}
									</ListGroup>
									</Accordion.Body>
								</Accordion.Item>)
							})}
						</Accordion>
					</Col>
				</Row>
			</Container>
		);
	}

export default Resume;