import React from 'react'; 
import 'bootstrap/dist/css/bootstrap.min.css';


function ProjectCards(props) {
    return (
      <div className="card"> 
        <div className="img-container">
          <img alt={props.name} src={props.image}/>
        </div>
        <div className="content">
              <p className="card-title">{props.name}</p>
              <p class="proj-icons-container">
              <a href={props.github}><img class="project-icon" src="https://img.icons8.com/external-vitaliy-gorbachev-lineal-color-vitaly-gorbachev/35/000000/external-space-station-space-vitaliy-gorbachev-lineal-color-vitaly-gorbachev.png" alt="GitHub Repo" />Github</a> 
              <a href={props.deploy}><img class="project-icon" src="https://img.icons8.com/external-icongeek26-flat-icongeek26/32/000000/external-space-shuttle-space-icongeek26-flat-icongeek26.png" alt="Live Application" />Application</a> 
       
             
             </p>
              <p class="topics">
                    ({props.topics})
                </p>
        </div>
      </div>
    );
}
  
  export default ProjectCards;