import React from "react";
import {Container, Row, Col} from "react-bootstrap";
import profilePic from '../assets/kalani.jpg';

function About() {
	return (
		<Container className="">
			<Row>
				<div className="jumbotron jumbotron-fluid mb-0">
				</div>
				<Col xs={8} sm={6} md={6} lg={3} className="center mx-auto mb-lg-5 mb-m-4 mb-3">
					<img
						src={profilePic}
						alt="Me"
						className="img-fluid rounded-circle"
					/>
				</Col>

				<Col xs={12} sm={6} md={6} className="text-center my-auto" id="about">
					<h3>
						Nice to Meet You!
					</h3>
					<p>
					I am a Front-end web developer who 
					recently earned a certificate in full stack development from UC San Diego, with newly developed skills in JavaScript, CSS, React.js, and responsive web design.
					Known as an innovative problem solver passionate about developing apps, with a focus on mobile-first design and development. 
					With each project, my aim is to best engage my clients for an impactful user experience. 
					I look forward to working with you. 
   					</p>
                    <p>
					Some skills I have to offer are: 
					<ul>
						<li>Front end skills such as HTML, CSS, Javascript, React</li>
						<li>Back end skills such as Node.js, Express.js, MySQL, MongoDB</li>

					</ul>
					</p>
				</Col>
			</Row>
		</Container>
	);
}

export default About;